.about {
  padding-bottom: 48px;
}

.about-top {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.about-info {
  font-size: 20px;
}

.info-name {
  color: blue;
  font-size: 24px;
}

@media only screen and (max-width: 720px) {
  .about-top {
    flex-direction: column;
    justify-content: center;
    align-items: center;
  }
}

.picture {
  height: max-content;
  margin: 4px;
}

